import {
  CheckCircle,
  Clock,
  Zap,
  Lock,
} from "lucide-react";

const statusUI = {
  available: {
    label: "Disponible",
    icon: Zap,
    ring: "ring-emerald-500/20",
  },
  active: {
    label: "Activo",
    icon: CheckCircle,
    ring: "ring-blue-500/30",
  },
  coming: {
    label: "Próximamente",
    icon: Clock,
    ring: "ring-zinc-700",
  },
};

export default function ModuleCard({ module }) {
  const StatusIcon = statusUI[module.status].icon;
  const isPro = module.tier === "pro";

  return (
    <div
      className="
        group relative rounded-2xl
        bg-gradient-to-b from-zinc-900/80 to-zinc-950
        border border-zinc-800
        p-6
        transition
        hover:border-zinc-700
        hover:shadow-2xl hover:shadow-black/40
      "
    >
      {/* Badge */}
      <div className="absolute top-4 right-4">
        <span
          className={`text-xs font-medium px-3 py-1 rounded-full
          ${
            isPro
              ? "bg-purple-500/10 text-purple-400"
              : "bg-emerald-500/10 text-emerald-400"
          }`}
        >
          {isPro ? "PRO" : "FREE"}
        </span>
      </div>

      {/* Header */}
      <div className="mb-4">
        <h3 className="text-lg font-semibold text-white tracking-tight">
          {module.name}
        </h3>
        <p className="mt-2 text-sm text-zinc-400 leading-relaxed">
          {module.description}
        </p>
      </div>

      {/* Status */}
      <div className="flex items-center gap-2 mb-6">
        <StatusIcon className="w-4 h-4 text-zinc-400" />
        <span className="text-xs text-zinc-400">
          {statusUI[module.status].label}
        </span>
      </div>

      {/* Footer / CTA */}
      <div className="mt-auto">
        {module.status === "available" && (
          <button
            className="
              w-full rounded-lg
              bg-white/10
              py-2.5 text-sm font-medium text-white
              transition
              hover:bg-white/20
            "
          >
            Activar módulo
          </button>
        )}

        {module.status === "active" && (
          <div className="flex items-center gap-2 text-sm text-blue-400">
            <CheckCircle className="w-4 h-4" />
            En uso
          </div>
        )}

        {module.status === "coming" && (
          <div className="flex items-center gap-2 text-sm text-zinc-500">
            <Lock className="w-4 h-4" />
            No disponible aún
          </div>
        )}
      </div>

      {/* Hover glow */}
      <div
        className="
          pointer-events-none absolute inset-0 rounded-2xl
          opacity-0 group-hover:opacity-100
          transition
          ring-1
          ${statusUI[module.status].ring}
        "
      />
    </div>
  );
}
